<template>
<div>
  <v-form>
    <v-container class="my-1 mx-5">
      <v-layout align-center>
        <v-flex>
          <v-text-field
            v-model="serverGroup"
            :rules="groupNameRules"
            :counter="10"
            label="Group Name"
            required
          ></v-text-field>
        </v-flex>

        <v-flex>
          <v-text-field
            v-model="serverName"
            :rules="groupNameRules"
            :counter="10"
            label="Server Name"
            required
          ></v-text-field>
        </v-flex>

        <v-flex>
          <v-text-field
            v-model="serverIp"
            :rules="groupNameRules"
            label="Server Ip"
            required
          ></v-text-field>
        </v-flex>
        <v-flex>
          <v-text-field
            v-model="serverUser"
            :rules="groupNameRules"
            label="Server User"
            required
          ></v-text-field>
        </v-flex>
        <v-flex>
          <v-text-field
            v-model="serverPassword"
            :rules="groupNameRules"
            label="Server Password"
            required
          ></v-text-field>
        </v-flex>
        <v-flex>
          <v-text-field
            v-model="serverPort"
            :rules="groupNameRules"
            label="Server Port"
            required
          ></v-text-field>
        </v-flex>                        
        <v-flex>
          <v-btn icon mx-0 >
            <v-icon v-on:click="addInfo()">done</v-icon>
          </v-btn>      
        </v-flex>
      </v-layout>
    </v-container>
  </v-form>
</div>  
</template>
<script>
  export default {
    data: () => ({
      valid: false,
      serverGroup : '',
      serverName : '',
      serverIp : '',
      serverUser : '',
      serverPassword : '',
      serverPort : '',      
      groupNameRules: [
        // v => !!v || 'Input is required',
        // v => v.length >= 3 || 'Input Data must be more than 3 characters'
      ],
      serverIpRule: [
         v => !!v || 'Ip Input is required',
         v => /.+'.'.+/.test(v) || 'E-mail must be valid'
      ]
      ,
      info : {serverGroup : '', serverName : '', serverIp : '', serverUser : '', serverPassword : '', serverPort : ''}
    })
    ,
    methods :
    {
      addInfo () {
        this.info.serverGroup = this.serverGroup;
        this.info.serverName = this.serverName
        this.info.serverIp = this.serverIp
        this.info.serverUser = this.serverUser;
        this.info.serverPassword = this.serverPassword
        this.info.serverPort = this.serverPort
        this.serverGroup = ''
        this.serverName  =''
        this.serverIp = ''
        this.serverPassword = ''
        this.serverUser  =''
        this.serverPort = ''        
        this.$emit('addInfo',this.info)

      }
    }
  }
</script>